/* eslint-disable react/jsx-no-target-blank */
/* eslint-disable radix */
/* eslint-disable react/no-array-index-key */
import React, { useContext, useState } from "react";
import { BrandContext } from "../BrandContext/BrandContext";
import PropTypes from "prop-types";
import clsx from "clsx";
import withStyles from "@material-ui/core/styles/withStyles";
import { Air, Clock, Close, Luggage } from "@cvccorp-components/icons";
import { Grid, Paper } from "@material-ui/core";
import moment from "moment";
import Typography from "../Typography";
import Box from "../Box";
import Button from "../Button";
import { styles } from "./styles";

const ModalDetailFlight = ({ classes, changeFlightOut, changeFlightBack, handleOpen, outwardFlight, backFlight }) => {
    // TODO rever implementação do contexto
    const ctx = useContext(BrandContext);

    const renderCardHeader = item => {
        function diffDateFormat(a, b) {
            const getHours = moment(a).diff(b, "hours");
            const getMinutes = moment(a).diff(b, "minutes");
            const formatMinutes = parseInt(getMinutes) % 60;
            return `${Math.abs(parseInt(getHours))}h${Math.abs(formatMinutes)}`;
        }
        return (
            item &&
            item.flights &&
            !!item.flights.length &&
            item.flights.map(
                (flight, index) =>
                    flight && (
                        <>
                            <Grid item xs={12} key={index}>
                                <header className={classes.header}>
                                    <img className={classes.ciaLogo} src={`https://almundo-com-res.cloudinary.com/image/upload/global/logos/digital-air-companies/${item && item.issuerCia}.svg`} alt={item.cia} />
                                    <div>
                                        <Typography type="headline6">
                                            Voo {flight.flightNumber} - {item.issuerCiaName}
                                        </Typography>
                                        <br />
                                        <Typography type="body2regular">Classe {flight.cabin}</Typography>
                                    </div>
                                </header>
                            </Grid>
                            <Grid item xs={12}>
                                <div className={clsx(classes.timeline, classes.departure)}>
                                    <div className={classes.icon} />
                                    <Grid container>
                                        <Grid item xs={12}>
                                            <Typography type="body1semibold">{moment(flight.departureDateTime).format("ddd[,] D [de] MMM [-] HH:mm")}</Typography>
                                            <br />
                                            <Typography type="body2">
                                                {flight.departureAirportDescription} ({flight.departureAirport})
                                            </Typography>
                                            <br />
                                        </Grid>
                                    </Grid>
                                </div>
                                <div className={clsx(classes.timeline, classes.contentTimeline)}>
                                    <br />
                                    <div style={{ color: "#999999" }}>
                                        {parseInt(flight.minutesDuration / 60)}h {flight.minutesDuration % 60}min de duração
                                    </div>
                                    <br />
                                </div>

                                <div className={clsx(classes.timeline, classes.arrival)}>
                                    <div className={classes.icon} />
                                    <Grid container>
                                        <Grid item xs={12}>
                                            <Typography type="body1semibold">{moment(flight.arrivalDateTime).format("ddd[,] D [de] MMM [-] HH:mm")}</Typography>
                                            <br />
                                            <Typography type="body2">
                                                {flight.arrivalAirportDescription} ({flight.arrivalAirport})
                                            </Typography>
                                            <br />
                                            <br />
                                        </Grid>
                                    </Grid>
                                </div>
                            </Grid>
                            <div className={classes.await}>
                                <Typography type="body2">
                                    <Clock /> Espera de {diffDateFormat(flight.arrivalDateTime, item && item.flights && !!item.flights.length && !!item.flights[index + 1] && item.flights[index + 1].departureDateTime)} em{" "}
                                    {flight.arrivalAirport} para troca de avião
                                </Typography>
                            </div>
                        </>
                    )
            )
        );
    };

    const renderBaggageCard = item => {
        return (
            item &&
            item.flights &&
            !!item.flights.length &&
            item.flights[0] &&
            item.flights[0].baggageInfo && (
                <Grid container>
                    <Grid item xs={4}>
                        <div className={item.flights[0].baggageInfo && clsx(classes.baggageCard, classes.active)}>
                            <Typography type="body1semibold">Bolsa Pequena</Typography>
                            <div>
                                <Typography type="body2semibold">1 item por adulto</Typography>
                                <br />
                                <Typography type="body2" style={{ color: "#999" }}>
                                    Deve caber embaixo do assento
                                </Typography>
                            </div>
                            <Typography type="caption">Peso não definido</Typography>
                        </div>
                    </Grid>
                    <Grid item xs={4}>
                        <div
                            className={item.flights[0].baggageInfo && item.flights[0].baggageInfo.handBaggageQuantity && item.flights[0].baggageInfo.handBaggageQuantity > 0 ? clsx(classes.baggageCard, classes.active) : classes.baggageCard}
                        >
                            <Typography type="body1semibold">Bagagem de mão</Typography>
                            <div>
                                <Typography type="body2semibold">{item.flights[0].baggageInfo.handBaggageQuantity} mala por adulto</Typography>
                                <br />
                                <Typography type="body2" style={{ color: "#999" }}>
                                    Deve caber no bagageiro de cabine
                                </Typography>
                            </div>
                            <Typography type="caption">
                                Até {item.flights[0].baggageInfo.handBaggageWeight && item.flights[0].baggageInfo.handBaggageWeight}
                                kg
                            </Typography>
                        </div>
                    </Grid>
                    <Grid item xs={4}>
                        <div
                            className={
                                item.flights[0].baggageInfo && item.flights[0].baggageInfo.checkedBaggageQuantity && item.flights[0].baggageInfo.checkedBaggageQuantity > 0 ? clsx(classes.baggageCard, classes.active) : classes.baggageCard
                            }
                        >
                            <Typography type="body1semibold">Bagagem despachada</Typography>
                            <div>
                                <Typography type="body2semibold">{item.flights[0].baggageInfo.checkedBaggageQuantity} malas por adulto</Typography>
                                <br />
                                <Typography type="body2" style={{ color: "#999" }}>
                                    Despachada no momento do check-in
                                </Typography>
                            </div>
                            <Typography type="caption">Até {item.flights[0].baggageInfo.checkedBaggageWeight}kg</Typography>
                        </div>
                    </Grid>
                </Grid>
            )
        );
    };

    return (
        <Paper>
            <Grid container align="center" style={{ paddingTop: "20px" }}>
                <Grid item xs={12}>
                    <div className={classes.container}>
                        <Grid container align="left">
                            <Grid item xs={11}>
                                <div>
                                    <Typography type="headline3">Detalhes do voo</Typography>
                                    <div>
                                        <>
                                            {outwardFlight &&
                                                outwardFlight.flights &&
                                                !!outwardFlight.flights.length &&
                                                outwardFlight.flights[0] &&
                                                outwardFlight.flights[0].baggageInfo &&
                                                outwardFlight.flights[0].baggageInfo.checkedBaggageQuantity > 0 &&
                                                outwardFlight.flights[0].baggageInfo.handBaggageQuantity > 0 && (
                                                <div className={classes.row}>
                                                    <Luggage size="medium" carryOn handBag checkedBag />
                                                    <div className={classes.baggageInfo}>
                                                        <Typography type="body1">Bagagens Inclusas</Typography>
                                                        <br />
                                                        <a href="#baggage_info" className={classes.link}>
                                                                Saiba mais
                                                        </a>
                                                    </div>
                                                </div>
                                            )}
                                            {outwardFlight &&
                                                outwardFlight.flights &&
                                                !!outwardFlight.flights.length &&
                                                outwardFlight.flights[0] &&
                                                outwardFlight.flights[0].baggageInfo &&
                                                outwardFlight.flights[0].baggageInfo.checkedBaggageQuantity === 0 &&
                                                outwardFlight.flights[0].baggageInfo.handBaggageQuantity > 0 && (
                                                <div className={classes.row}>
                                                    <Luggage size="medium" carryOn handBag />
                                                    <div className={classes.baggageInfo}>
                                                        <Typography type="body1">Bagagem de mão inclusa</Typography>
                                                        <br />
                                                        <a href="#baggage_info" className={classes.link}>
                                                                Saiba mais
                                                        </a>
                                                    </div>
                                                </div>
                                            )}
                                            {/* {outwardFlight &&
                        outwardFlight.flights &&
                        !!outwardFlight.flights.length &&
                        outwardFlight.flights[0] &&
                        outwardFlight.flights[0].baggageInfo &&
                        outwardFlight.flights[0].baggageInfo
                          .checkedBaggageQuantity &&
                        outwardFlight.flights[0].baggageInfo
                          .checkedBaggageQuantity > 0 &&
                        outwardFlight.flights[0].baggageInfo
                          .handBaggageQuantity === 0 && (
                          <Typography type="body1">
                            <Luggage size="medium" carryOn handBag />
                            Bagagem de mão inclusa
                          </Typography>
                        )} */}
                                            {outwardFlight &&
                                                outwardFlight.flights &&
                                                !!outwardFlight.flights.length &&
                                                outwardFlight.flights[0] &&
                                                outwardFlight.flights[0].baggageInfo &&
                                                outwardFlight.flights[0].baggageInfo.checkedBaggageQuantity &&
                                                outwardFlight.flights[0].baggageInfo.checkedBaggageQuantity === 0 &&
                                                outwardFlight.flights[0].handBaggageQuantity &&
                                                outwardFlight.flights[0].handBaggageQuantity === 0 ? (
                                                    <div className={classes.row}>
                                                        <Typography type="body1">
                                                            <Luggage size="medium" carryOn />
                                                            <br />
                                                        Sem Bagagem inclusas
                                                            <a>Saiba mais</a>
                                                        </Typography>
                                                    </div>
                                                ) : null}
                                        </>
                                    </div>
                                </div>
                            </Grid>
                            <Grid item xs={1}>
                                <Box className={classes.root} onClick={() => handleOpen()}>
                                    <Close />
                                </Box>
                            </Grid>
                            {outwardFlight && !!Object.keys(outwardFlight).length && (
                                <Grid item xs={12}>
                                    <div className={classes.card}>
                                        <Grid container>
                                            <Grid item xs={12}>
                                                <div className={classes.cardHeader}>
                                                    <div>
                                                        <Air />
                                                        <Typography type="headline4">Voo de Ida</Typography>
                                                    </div>
                                                    {/* <Button
                            className={classes.detailBtn}
                            outline
                            label="Alterar voo"
                            variant="text"
                            onClick={() => changeFlightOut()}
                          /> */}
                                                </div>
                                            </Grid>
                                            <Grid item xs={12}>
                                                {renderCardHeader(outwardFlight)}
                                            </Grid>
                                        </Grid>
                                    </div>
                                </Grid>
                            )}
                            {backFlight && !!Object.keys(backFlight).length && (
                                <Grid item xs={12}>
                                    <div className={classes.card}>
                                        <Grid container>
                                            <Grid item xs={12}>
                                                <div className={classes.cardHeader}>
                                                    <div>
                                                        <Air />
                                                        <Typography type="headline4">Voo de Volta</Typography>
                                                    </div>
                                                    {/* <Button
                            className={classes.detailBtn}
                            outline
                            label="Alterar voo"
                            variant="text"
                            onClick={() => changeFlightBack()}
                          /> */}
                                                </div>
                                            </Grid>
                                            <Grid item xs={12}>
                                                {renderCardHeader(backFlight)}
                                            </Grid>
                                        </Grid>
                                    </div>
                                </Grid>
                            )}
                            <Grid item xs={12} align="left" id="baggage_info">
                                <Typography type="headline5">Entendendo suas bagagens inclusas</Typography>
                            </Grid>
                            <Grid item xs={12} style={{ marginBottom: "30px" }}>
                                <Typography type="body2" style={{ color: "#999" }}>
                                    Em todo voo você tem direito a levar uma pequena bolsa com seus pertences
                                </Typography>
                            </Grid>
                        </Grid>
                        <Grid item xs={12}>
                            {renderBaggageCard(outwardFlight)}
                        </Grid>
                        <Grid container align="left" className={classes.footer}>
                            <Grid item xs={12} className={classes.footerText}>
                                <Typography type="body2">Para detalhes sobre dimensões permitidas, consultar sua companhia aérea.</Typography>
                            </Grid>
                            <Grid item xs={12}>
                                <Typography type="body2">
                                    A <strong>{ctx.name}</strong> disponibiliza em cumprimento à <strong>Resolução ANAC 218</strong> - que estabelece a tranparência e divulgação dos percentuais de atrasos e cancelamento de voos do
                                    transporte aéreo de passageiros no Brasil, o link com as estatísticas fornecidas pela Agência Nacional de Aviação Civil:
                                    <a href="http://anac.gov.br/atrasosecancelamentos" target="_blank" style={{ textDecoration: "none" }}>
                                        anac.gov.br/atrasosecancelamentos
                                    </a>
                                    .
                                    <br />
                                    <br />
                                    Lembramos que a informação estará disponível mensalmente e refere-se sempre ao mês anterior.
                                </Typography>
                            </Grid>
                        </Grid>
                    </div>
                </Grid>
            </Grid>
        </Paper>
    );
};

ModalDetailFlight.propTypes = {
    classes: PropTypes.object,
    onOpen: PropTypes.func,
    onClose: PropTypes.func,
    isOpen: PropTypes.bool,
    title: PropTypes.string,
    children: PropTypes.object
};

export default withStyles(styles, { withTheme: true })(ModalDetailFlight);
